import axios from 'axios'
import React, { useEffect, useState } from 'react'
import useURL from '../useURL/useURL'
import { Link } from 'react-router-dom'

export default function Home() {

    let{data,isVisible,scrollToTopButton}=useURL(`https://free-to-play-games-database.p.rapidapi.com/api/games`)

    async function coming(){
        window.alert("Coming")
    }
    useEffect(()=>{
        document.title="Game Over - Discover The Best Games"
    },[])
  return (
  <>{data !== null ? <div className="container customContainer text-center">
    <div className="header d-flex justify-content-center align-items-center my-5" style={{ backgroundImage:`url(https://www.freetogame.com/assets/images/paladins.png)` }}>
        <div className="headerContent">
            <h1 className=' h-auto'>Discover the best <span className=' text-info'>Game Over</span> games!</h1>
            <div>Track what you've played and search for what to play next! Plus get free premium loot!</div>
        </div>
    </div>
  <div className="row">
  {data.map((game)=>
        <div key={game.id}className=" col-6 col-sm-4 col-md-4 my-3 game-card mouse-pointer">
        <Link className="text-decoration-none" to={`/game/${game.id}`}>
        <div  id='card' className="form-color rounded ">


            <img id={game.id} src={game.thumbnail} className="w-100" alt="game_image" />
                  <div className="game-content px-2 py-2">
                      <div className="d-flex align-items-center justify-content-between">
                          {game.title.length > 10 ? <>
                              <h3 className="text-gray h4 g-card-text-size" title={game.title}>{game.title.split("").slice(0, 10).join("")}...</h3>
                          </> : <>
                              <h3 className="title text-gray h4 g-card-text-size">{game.title}</h3>
                          </>}
                          <p className="bg-light-blue fs-small rounded py-1 px-2 mt-1  text-danger fw-bold">FREE</p>
                      </div>
                      <div>
                          <p className="text-muted" title={game.short_description}>
                              {game.short_description.split("").slice(0, 30).join("")}....
                          </p>
                      </div>
                      <div className="text-muted d-flex justify-content-between align-items-center">
                          <i className="fas fa-plus-square" onClick={coming} title='Add to Favorite'></i>
                          <div>
                              <span className="badge rounded-3 bg-secondary text-white  me-2">{game.genre}</span>
                              {game.platform === "Web Browser" ? <i className="fa-solid fa-window-maximize text-muted" title='Web Browser'></i> :<i className="fab fa-windows text-muted " title='PC (Windows)'></i> }
                              
                          </div>
                      </div>
                  </div>
              </div>
        </Link>
      </div>
  ).slice(0,12)}
  <button style={{ display: isVisible ? 'block' : 'none' }} className='btn btn-outline-secondary position-fixed p-0 mb-4 w-auto start-0 bottom-0 toTop' onClick={scrollToTopButton}><i className="fa-solid fa-arrow-up"></i> <br/> Scroll<br/>to<br/>Top</button>
  </div>
  </div>:""}</>)
}
