import axios from 'axios'
import React, {useEffect, useState } from 'react'
import { ChangeAny } from '../Navbar/change'

export default function useURL(url) {

    let[data,setData]=useState([])
    let [isVisible, setIsVisible] = useState(false);

    let[endIndex, setEndIndex] = useState(20)

    useEffect(()=>{
        getData()
        let toggleVisibility = () => {
            if (window.scrollY > 100) {
              setIsVisible(true);
            } else {
              setIsVisible(false);
            }
          };
          window.addEventListener('scroll', toggleVisibility);
        
          return () => {
            window.removeEventListener('scroll', toggleVisibility);
          };
    },[url])

    function scrollToTopButton() {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }

    async function getData(){
        let {data} = await axios.get(url,
            {headers:{
                'X-RapidAPI-Key': '72b0ecfd5amsh2bfb620237e11fep1b6c9bjsne048c6187ad2',
                'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        }})
        setData(data)
    }

    function sliceData() {
        setEndIndex(endIndex + 20)
        if(endIndex > data.length) {
          setEndIndex(data.length)
        }
      }

  return ({data,sliceData,endIndex,isVisible,scrollToTopButton})
}
