import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import './App.css';
import MasterLayout from './Components/MasterLayout/MasterLayout';
import Home from './Components/Home/Home';
import About from './Components/About/About';
import GameDetails from './Components/GameDetails/GameDetails';
import Login from './Components/Login/Login';
import Registration from './Components/Registration/Registration';
import Notfound from './Components/Notfound/Notfound';
import { ChangeAnyProvider } from './Components/Navbar/change';
import Category from './Components/Category/Category';
import Platform from './Components/Platform/Platform';
import Sort from './Components/Sort/Sort';
import Custom from './Components/Custom/Custom';

function App() {




  let Routes = createHashRouter([
    {path:'/',element:<MasterLayout/>,
      children:[
        {path:'/',element:<Home/>},
        {path:'home',element:<Home/>},
        {path:'about',element:<About/>},
        {path:'category',element:<Category/>},
        {path:'platform',element:<Platform/>},
        {path:'custom',element:<Custom/>},
        {path:'sort',element:<Sort/>},
        {path:'game/:id',element:<GameDetails/>},
        {path:'login',element:<Login/>},
        {path:'registration',element:<Registration/>},
        {path:'*',element:<Notfound/>}
      ]}
  ])

  return (
    <ChangeAnyProvider>
      <RouterProvider router={Routes}/>       {/* // we passed Routes to  RouterProvider as props   */}
    </ChangeAnyProvider>
  );
}

export default App;
