/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import { ChangeAny } from './change'
import img from '../../Image/logo.png'
export default function Navbar() {
let{changePlatform,changeSort}=useContext(ChangeAny)
let[categoryList]=useState(['mmorpg', 'shooter', 'strategy', 'moba', 'racing', 'sports', 'social', 'sandbox', 'open-world', 'survival', 'pvp', 'pve', 'pixel', 'voxel', 'zombie', 'turn-based', 'first-person', 'third-Person', 'top-down', 'tank', 'space', 'sailing', 'side-scroller', 'superhero', 'permadeath', 'card', 'battle-royale', 'mmo', 'mmofps', 'mmotps', '3d', '2d', 'anime', 'fantasy', 'sci-fi', 'fighting', 'action-rpg', 'action', 'military', 'martial-arts', 'flight', 'low-spec', 'tower-defense', 'horror', 'mmorts'])
let[platformList]=useState(['pc','browser'])
let[sortList]=useState(['release-date', 'popularity', 'alphabetical' , 'relevance'])

  return (<>
  <nav className="navbar navbar-expand-lg navbar-bg-color shadow-lg text-white position-fixed w-100">
    <div className="container">
      <Link className="navbar-brand d-flex justify-content-center align-items-center" to={'home'}>
        <img style={{ height:"45px" }} src={img}/>
        <span className='mx-2'>Game Over</span>
        </Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto">
          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" to={'home'}>Home</Link>
          </li>
          <li className="nav-item dropdown">
            <Link className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Sort By
            </Link>
            <ul className="dropdown-menu ">
              {sortList.map((sortName,i)=><li key={i}><Link onClick={()=>changeSort(sortName)} className="dropdown-item" to={'sort'}>{sortName}</Link></li>).slice(0,10)}
            </ul>
          </li>
          <li className="nav-item dropdown">
            <Link className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Platform
            </Link>
            <ul className="dropdown-menu ">
              {platformList.map((platformName,i)=><li key={i}><Link onClick={()=>changePlatform(platformName)} className="dropdown-item" to={'platform'}>{platformName}</Link></li>).slice(0,10)}
            </ul>
          </li>
          <li className="nav-item dropdown">
            <Link className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Category
            </Link>
            <ul className="dropdown-menu ">
              {categoryList.map((categoryName,i)=><li key={i}><Link className="dropdown-item" to={'category'}>{categoryName}</Link></li>).slice(0,10)}
              <li><hr className='m-0'/></li>
              <li><Link className="dropdown-item seeAll" to={'category'}>See All Categorys</Link></li>
            </ul>
          </li>
          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" to={'custom'}>Custom Filter</Link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  </>)
}
