import React, { useContext, useEffect } from 'react'
import { ChangeAny } from '../Navbar/change'
import useURL from '../useURL/useURL'
import { Link } from 'react-router-dom'

export default function Sort() {

  let{sort,changeSort}=useContext(ChangeAny)
  
  let{data,sliceData,endIndex,isVisible,scrollToTopButton}= useURL(`https://free-to-play-games-database.p.rapidapi.com/api/games?sort-by=${sort}`)


useEffect(()=>{
  document.title ="Sort"
},[data])

  return (
    <>{data !== null ? <div className="container customContainer">
  <div className="category d-flex justify-content-center w-100 flex-wrap my-4">
    <span onClick={()=>changeSort('release-date')} id='release-date' className="btn btn-dark m-2">Release Date</span>
    <span onClick={()=>changeSort('popularity')} id='popularity' className="btn btn-dark m-2">Popularity</span>
    <span onClick={()=>changeSort('alphabetical ')} id='alphabetical ' className="btn btn-dark m-2">Alphabetical </span>
    <span onClick={()=>changeSort('relevance')} id='relevance' className="btn btn-dark m-2">Relevance</span>
  </div>
    <div className="row justify-content-center">
    {data.map((game)=>
          <div key={game.id} className=" col-6 col-sm-4 col-md-3 my-3 game-card mouse-pointer">
          <Link className="text-decoration-none" to={`/game/${game.id}`}>
            <div className="form-color rounded ">
              <img src={game.thumbnail} className="w-100" alt="game_image" />
              <div className="game-content px-2 py-2">
                  <div className="d-flex align-items-center justify-content-between">
                      {game.title.length > 10 ? <>
                          <h3 className="text-gray h4 g-card-text-size" title={game.title}>{game.title.split("").slice(0, 10).join("")}...</h3>
                      </> : <>
                          <h3 className="title text-gray h4 g-card-text-size">{game.title}</h3>
                      </>}
                      <p className="bg-light-blue fs-small rounded py-1 px-2 mt-1  text-danger fw-bold">FREE</p>
                  </div>
                  <div>
                      <p className="text-muted" title={game.short_description}>
                          {game.short_description.split("").slice(0, 30).join("")}....
                      </p>
                  </div>
                  <div className="text-muted d-flex justify-content-between align-items-center">
                      <i className="fas fa-plus-square" title='Add to Favorite'></i>
                      <div>
                          <span className="badge rounded-3 bg-secondary text-white  me-2">{game.genre}</span>
                          {game.platform === "Web Browser" ? <i className="fa-solid fa-window-maximize text-muted" title='Web Browser'></i> :<i className="fab fa-windows text-muted " title='PC (Windows)'></i> }
                      </div>
                  </div>
              </div>
            </div>
          </Link>
        </div>
    ).slice(0, endIndex)}
    
    {data.length > 20 && endIndex < data.length ? 
        <button onClick={sliceData} className="btn btn-outline-secondary px-4 w-auto  py-2 my-2 see-more-btn">See More</button>
        
    : null}

    <button style={{ display: isVisible ? 'block' : 'none' }} className='btn btn-outline-secondary position-fixed p-0 mb-4 w-auto start-0 bottom-0 toTop' onClick={scrollToTopButton}><i className="fa-solid fa-arrow-up"></i> <br/> Scroll<br/>to<br/>Top</button>

    </div>
    </div>:""}</>
  )

}
