/* eslint-disable jsx-a11y/img-redundant-alt */
import React, { useEffect, useState } from 'react'
import ShowMoreText from "react-show-more-text";
import useURL from '../useURL/useURL'
import { Link, useParams } from 'react-router-dom'

export default function GameDetails() {
  let {id} = useParams()
  let{data}=useURL(`https://free-to-play-games-database.p.rapidapi.com/api/game?id=${id}`)

  useEffect(()=>{
    console.log(data.minimum_system_requirements);
  },[data])
  return (<>
  {data !== null ? 
  <div className="container customContainer">


    <div className="backgroundImage" style={{ backgroundImage:`url(https://www.freetogame.com/g/${id}/background.jpg)` }}>
      <div className="linearBackground"></div>
    </div>



    <div className="row">

      <div className="col-8 col-md-4 position-relative">
        <div className="mediaContainer position-sticky">
          <div className="imgBox w-100 shadow-lg my-3">
          <img src={data.thumbnail} alt={`${data.title} Image`} className='w-100' />
          </div>
          <div className="playNow row w-100 my-3 m-auto">
          <div className="btn btn-danger rounded-0 rounded-start col-6 col-md-4">Free</div>
          <Link className='col-6 col-md-8 p-0' to={data.freetogame_profile_url} target='blank'>
            <div className="btn btn-primary rounded-0 rounded-end w-100">Play Now<i class="fa-solid fa-arrow-right fa-bold ps-3"></i></div>
          </Link>
          </div>
        </div>
      </div>


      <div className="col-12 col-md-8">
        <h1>{data.title}</h1>

        <div className="short mb-5 mt-3">
          <span className=''>{data.short_description}</span>
        </div>

        <div className="about my-5">
          <h4>About {data.title}</h4>
          <ShowMoreText lines={2}  more="Show more" less="Show less" className="content-css">
            <p>{data.description}</p>
          </ShowMoreText>
        </div>

        <div className="additionalInfo">
          <h4>Additional Information</h4>
          <div className="info row">
              <div className="col-6 col-md-4 my-3 title">
                <h6 className='text-muted'>Title</h6>
                <span>{data.title}</span>
              </div>
              <div className="col-6 col-md-4 my-3 release">
                <h6 className='text-muted'>Release Date</h6>
                <span>{data.release_date}</span>
              </div>
              <div className="col-6 col-md-4 my-3 platform">
                <h6 className='text-muted'>Platform</h6>
                <span>{data.platform === "Web Browser" ? <i className="fa-solid fa-window-maximize text-muted" title='Web Browser'></i> :<i className="fab fa-windows text-muted " title='PC (Windows)'></i> } {data.platform}</span>
              </div>
              <div className="col-6 col-md-4 my-3 publisher">
                <h6 className='text-muted'>Publisher</h6>
                <span>{data.publisher}</span>
              </div>
              <div className="col-6 col-md-4 my-3 developer">
                <h6 className='text-muted'>Developer</h6>
                <span>{data.developer}</span>
              </div>
              <div className="col-6 col-md-4 my-3 genre">
                <h6 className='text-muted'>Genre</h6>
                <span>{data.genre}</span>
              </div>
          </div>
        </div>

        <div className="screenSlider my-5">
          <h4 className='my-3'>Screenshoots</h4>
          <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
            <div class="carousel-inner">
              {data.length == 0 ? "" : <>
                {data.screenshots.map((screen,i)=>
                  <div key={i} class={`carousel-item ${i==0 ? "active" : ""}`}>
                    <img class="d-block w-100" src={screen.image} alt={`slide ${i}`}/>
                  </div>
                )}
              </>}
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Next</span>
            </button>
          </div>
        </div>
        {data.length == 0 ? "" : 
          <div className="systemRequirements">
            <h4>Minimum System Requirements</h4>
            {data.minimum_system_requirements !== undefined ? 
              <div className="requirements">
                <div className="my-4 os">
                  <h6 className='text-muted'>Os</h6>
                  <span>{data.minimum_system_requirements.os}</span>
                </div>
                <div className="my-4 processor">
                  <h6 className='text-muted'>Processor</h6>
                  <span>{data.minimum_system_requirements.processor}</span>
                </div>
                <div className="my-4 memory">
                  <h6 className='text-muted'>Memory</h6>
                  <span>{data.minimum_system_requirements.memory}</span>
                </div>
                <div className="my-4 graphics">
                  <h6 className='text-muted'>Graphics</h6>
                  <span>{data.minimum_system_requirements.graphics}</span>
                </div>
                {data.minimum_system_requirements.storage == "?" ? "" : 
                  <div className="my-4 storage">
                    <h6 className='text-muted'>Storage</h6>
                    <span>{data.minimum_system_requirements.storage}</span>
                  </div>
                }
                <div className="my-4 additionalNotes">
                  <h6 className='text-muted'>Additional Notes</h6>
                  <span>Specifications may change during development</span>
                </div>
              </div>
            : 
              <div>{data.title} is a browser based game and should run smoothly on practically any PC with a updated web-browser.If you have old hardware or software, you may still be able to play {data.title}, but your game experience may suffer. For the best gameplay experience, we recommend the latest versions of Firefox, Chrome, or Internet Explorer.</div>
            }
          </div>
        }
      </div>
    </div>
  </div>
  :""}</>)
}
