import { createContext, useState } from "react";

export let ChangeAny = createContext()


export function ChangeAnyProvider(props){
    let[platform,setPlatform]=useState('pc')
    let[sort,setSort]=useState('release-date')
    let[category,setCategory]=useState('shooter')
    let[custom,setCustom]=useState(['pc','shooter','release-date'])

    function changePlatform(type){
        setPlatform(type)
    }
    function changeSort(type){
        setSort(type)
    }
    function changeCategory(type){
        setCategory(type)
    }
    
    function changeCustom(p,c,s){
        setCustom([p,c,s])
    }

    let contextValues={
        changePlatform,
        platform,
        changeSort,
        sort,
        changeCategory,
        category,
        changeCustom,
        custom
    }

    return <ChangeAny.Provider value={contextValues}>{props.children}</ChangeAny.Provider>
}